import { create } from 'zustand';

export interface Trade {
    id: string;
    pair: string;
    side: 'BUY' | 'SELL';
    entry: number;
    current: number;
    sl: number;
    tp: number;
    lots: number;
    pnl: number;
    openTime: Date;
    isLocked?: boolean;
}

export interface Signal {
    id: string;
    pair: string;
    side: 'BUY' | 'SELL';
    entry: number;
    sl: number;
    tp1: number;
    tp2: number;
    tp3: number;
    confidence: number;
}

export interface RiskSettings {
    riskProfile: 'conservative' | 'balanced' | 'aggressive';
    maxDrawdown: number;
    autoBreakeven: boolean;
    aiTrail: boolean;
    tpTrigger: 1 | 2 | 3;
}

export interface TradingState {
    // Account
    balance: number;
    equity: number;
    margin: number;

    // Positions
    openTrades: Trade[];
    lockedTrades: Trade[];

    // Settings
    riskSettings: RiskSettings;

    // MT5 Connection
    isConnected: boolean;
    latency: number;

    // Actions
    setBalance: (balance: number) => void;
    setEquity: (equity: number) => void;
    setConnectionStatus: (isConnected: boolean, latency: number) => void;
    addTrade: (trade: Trade) => void;
    removeTrade: (tradeId: string) => void;
    lockTrade: (tradeId: string) => void;
    unlockTrade: (tradeId: string) => void;
    updateRiskSettings: (settings: Partial<RiskSettings>) => void;
}

export const useTradingStore = create<TradingState>((set) => ({
    // Initial State
    balance: 7310.56,
    equity: 7310.56,
    margin: 0,
    openTrades: [],
    lockedTrades: [],
    riskSettings: {
        riskProfile: 'balanced',
        maxDrawdown: 15,
        autoBreakeven: true,
        aiTrail: false,
        tpTrigger: 2
    },
    isConnected: false,
    latency: 18,

    // Actions
    setBalance: (balance) => set({ balance }),
    setEquity: (equity) => set({ equity }),
    setConnectionStatus: (isConnected, latency) => set({ isConnected, latency }),

    addTrade: (trade) => set((state) => ({
        openTrades: [...state.openTrades, trade]
    })),

    removeTrade: (tradeId) => set((state) => ({
        openTrades: state.openTrades.filter(t => t.id !== tradeId),
        lockedTrades: state.lockedTrades.filter(t => t.id !== tradeId)
    })),

    lockTrade: (tradeId) => set((state) => {
        const trade = state.openTrades.find(t => t.id === tradeId);
        if (!trade) return state;

        return {
            openTrades: state.openTrades.filter(t => t.id !== tradeId),
            lockedTrades: [...state.lockedTrades, { ...trade, isLocked: true }]
        };
    }),

    unlockTrade: (tradeId) => set((state) => {
        const trade = state.lockedTrades.find(t => t.id === tradeId);
        if (!trade) return state;

        return {
            lockedTrades: state.lockedTrades.filter(t => t.id !== tradeId),
            openTrades: [...state.openTrades, { ...trade, isLocked: false }]
        };
    }),

    updateRiskSettings: (settings) => set((state) => ({
        riskSettings: { ...state.riskSettings, ...settings }
    }))
}));
