import React, { useEffect, useRef } from 'react';

declare global {
    interface Window {
        TradingView: any;
    }
}

const TradingViewWidget: React.FC = () => {
    const container = useRef<HTMLDivElement>(null);

    useEffect(() => {
        const script = document.createElement("script");
        script.src = "https://s3.tradingview.com/tv.js";
        script.type = "text/javascript";
        script.async = true;
        script.onload = () => {
            if (container.current && window.TradingView) {
                new window.TradingView.widget({
                    "width": "100%",
                    "height": 500,
                    "symbol": "BINANCE:BTCUSDT",
                    "interval": "D",
                    "timezone": "Etc/UTC",
                    "theme": "dark",
                    "style": "1",
                    "locale": "en",
                    "toolbar_bg": "#f1f3f6",
                    "enable_publishing": false,
                    "allow_symbol_change": true,
                    "container_id": "tradingview_widget"
                });
            }
        };
        document.head.appendChild(script);
        return () => { script.remove(); };
    }, []);

    return (
        <div className="tradingview-container glass-panel" style={{ height: '550px', padding: '10px' }}>
            <div id="tradingview_widget" ref={container} style={{ height: 'calc(100% - 40px)' }} />
            <div className="tradingview-footer" style={{ fontSize: '10px', color: 'var(--secondary)', marginTop: '10px', textAlign: 'center' }}>
                Live Markets via TradingView Engine
            </div>
        </div>
    );
};

export default TradingViewWidget;
