"use client";
import React, { useEffect, useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Brain, TrendingUp, AlertTriangle, Lightbulb, Activity, Zap } from 'lucide-react';

interface AIInsight {
    type: 'suggestion' | 'warning' | 'analysis';
    message: string;
    confidence: number;
}

export const SentinelAI = () => {
    const [insights, setInsights] = useState<AIInsight[]>([
        { type: 'analysis', message: 'Market volatility increasing. Consider tightening stop loss.', confidence: 87 },
        { type: 'suggestion', message: 'XAUUSD showing bullish divergence on 4H chart.', confidence: 92 },
        { type: 'warning', message: 'Account drawdown at 8%. Approaching risk threshold.', confidence: 95 }
    ]);
    const [pulse, setPulse] = useState(true);

    useEffect(() => {
        const interval = setInterval(() => {
            setPulse(p => !p);
        }, 1500);
        return () => clearInterval(interval);
    }, []);

    const getIcon = (type: string) => {
        switch (type) {
            case 'suggestion': return <Lightbulb size={16} className="text-blue-400" />;
            case 'warning': return <AlertTriangle size={16} className="text-red-400" />;
            case 'analysis': return <TrendingUp size={16} className="text-purple-400" />;
            default: return <Brain size={16} />;
        }
    };

    const getColorClass = (type: string) => {
        switch (type) {
            case 'suggestion': return 'border-blue-500/30 bg-blue-500/5';
            case 'warning': return 'border-red-500/30 bg-red-500/5';
            case 'analysis': return 'border-purple-500/30 bg-purple-500/5';
            default: return 'border-white/10 bg-white/5';
        }
    };

    return (
        <motion.div
            initial={{ x: -100, opacity: 0 }}
            animate={{ x: 0, opacity: 1 }}
            className="glass-glamor-heavy rounded-3xl p-6 h-full flex flex-col"
        >
            {/* Header */}
            <div className="flex items-center justify-between mb-6">
                <div className="flex items-center gap-3">
                    <div className="relative">
                        <Brain size={24} className="text-purple-400" />
                        <motion.div
                            animate={{ scale: pulse ? 1.2 : 1, opacity: pulse ? 0 : 0.5 }}
                            transition={{ duration: 1.5 }}
                            className="absolute inset-0 rounded-full bg-purple-500 blur-md"
                        />
                    </div>
                    <div>
                        <h3 className="text-lg font-bold text-white">Sentinel AI</h3>
                        <p className="text-xs text-gray-500">Live Market Analysis</p>
                    </div>
                </div>
                <div className="pulse-data">
                    <Activity size={20} className="text-green-400" />
                </div>
            </div>

            {/* Data Pulse Visualization */}
            <div className="mb-6 p-4 rounded-2xl glass-glamor border border-purple-500/20">
                <div className="flex items-center justify-between mb-2">
                    <span className="text-xs font-bold text-purple-400 uppercase tracking-wider">Neural Activity</span>
                    <span className="text-xs text-gray-500">Real-time</span>
                </div>
                <div className="flex gap-1 h-12 items-end">
                    {[...Array(20)].map((_, i) => (
                        <motion.div
                            key={i}
                            className="flex-1 bg-gradient-to-t from-purple-500 to-blue-500 rounded-sm"
                            animate={{
                                height: `${Math.random() * 100}%`,
                            }}
                            transition={{
                                duration: 0.5,
                                repeat: Infinity,
                                repeatType: "reverse",
                                delay: i * 0.05
                            }}
                        />
                    ))}
                </div>
            </div>

            {/* Account Metrics */}
            <div className="grid grid-cols-2 gap-3 mb-6">
                <div className="glass-glamor rounded-xl p-3">
                    <div className="text-xs text-gray-500 mb-1">Win Rate</div>
                    <div className="text-xl font-bold text-green-400">84.2%</div>
                </div>
                <div className="glass-glamor rounded-xl p-3">
                    <div className="text-xs text-gray-500 mb-1">Risk Score</div>
                    <div className="text-xl font-bold text-yellow-400">6.5/10</div>
                </div>
            </div>

            {/* AI Insights */}
            <div className="flex-1 overflow-y-auto space-y-3 custom-scrollbar">
                <div className="text-xs font-bold text-gray-500 uppercase tracking-wider mb-2">Active Insights</div>
                <AnimatePresence>
                    {insights.map((insight, i) => (
                        <motion.div
                            key={i}
                            initial={{ opacity: 0, y: 20 }}
                            animate={{ opacity: 1, y: 0 }}
                            exit={{ opacity: 0, x: -100 }}
                            transition={{ delay: i * 0.1 }}
                            className={`rounded-xl border p-4 ${getColorClass(insight.type)}`}
                        >
                            <div className="flex items-start gap-3">
                                <div className="mt-0.5">{getIcon(insight.type)}</div>
                                <div className="flex-1">
                                    <p className="text-sm text-white leading-relaxed mb-2">{insight.message}</p>
                                    <div className="flex items-center gap-2">
                                        <div className="flex-1 h-1 bg-white/10 rounded-full overflow-hidden">
                                            <motion.div
                                                initial={{ width: 0 }}
                                                animate={{ width: `${insight.confidence}%` }}
                                                transition={{ duration: 1, delay: i * 0.1 }}
                                                className="h-full bg-gradient-to-r from-purple-500 to-blue-500"
                                            />
                                        </div>
                                        <span className="text-xs text-gray-500 font-mono">{insight.confidence}%</span>
                                    </div>
                                </div>
                            </div>
                        </motion.div>
                    ))}
                </AnimatePresence>
            </div>

            {/* AI Status */}
            <div className="mt-6 pt-4 border-t border-white/10">
                <div className="flex items-center justify-between text-xs">
                    <div className="flex items-center gap-2">
                        <div className="h-2 w-2 rounded-full bg-green-500 animate-pulse" />
                        <span className="text-gray-500">AI Active</span>
                    </div>
                    <div className="flex items-center gap-1 text-gray-600">
                        <Zap size={12} />
                        <span>18ms latency</span>
                    </div>
                </div>
            </div>
        </motion.div>
    );
};
