"use client";
import React from 'react';
import { motion } from 'framer-motion';
import { Trophy, TrendingUp, Target, Zap } from 'lucide-react';

interface Trader {
    rank: number;
    name: string;
    avatar: string;
    winRate: number;
    roi: number;
    totalPips: number;
    followers: number;
}

const MOCK_TRADERS: Trader[] = [
    { rank: 1, name: 'Alex "The Bull" Morgan', avatar: 'AM', winRate: 94.2, roi: 187.5, totalPips: 12450, followers: 2341 },
    { rank: 2, name: 'Sarah "Precision" Chen', avatar: 'SC', winRate: 91.8, roi: 165.3, totalPips: 10890, followers: 1876 },
    { rank: 3, name: 'Marcus "Flow" Williams', avatar: 'MW', winRate: 89.5, roi: 142.7, totalPips: 9230, followers: 1542 },
    { rank: 4, name: 'Elena Rodriguez', avatar: 'ER', winRate: 87.3, roi: 128.4, totalPips: 8100, followers: 1203 },
    { rank: 5, name: 'David Kim', avatar: 'DK', winRate: 85.1, roi: 115.2, totalPips: 7450, followers: 987 }
];

export const TopTierLeaderboard = () => {
    const getMedalGradient = (rank: number) => {
        switch (rank) {
            case 1: return 'bg-gradient-gold';
            case 2: return 'bg-gradient-silver';
            case 3: return 'bg-gradient-bronze';
            default: return 'bg-gray-700';
        }
    };

    const getMedalGlow = (rank: number) => {
        switch (rank) {
            case 1: return 'glow-gold';
            case 2: return 'shadow-lg shadow-gray-400/20';
            case 3: return 'shadow-lg shadow-orange-400/20';
            default: return '';
        }
    };

    return (
        <motion.div
            initial={{ x: 100, opacity: 0 }}
            animate={{ x: 0, opacity: 1 }}
            className="glass-glamor-heavy rounded-3xl p-6 h-full flex flex-col"
        >
            {/* Header */}
            <div className="flex items-center justify-between mb-6">
                <div className="flex items-center gap-3">
                    <Trophy size={24} className="text-yellow-400" />
                    <div>
                        <h3 className="text-lg font-bold text-white">Top Tier</h3>
                        <p className="text-xs text-gray-500">Elite copy-traders</p>
                    </div>
                </div>
                <div className="px-3 py-1 rounded-full bg-yellow-500/10 border border-yellow-500/30 text-xs font-bold text-yellow-400">
                    Live Rankings
                </div>
            </div>

            {/* Leaderboard */}
            <div className="flex-1 overflow-y-auto space-y-3 custom-scrollbar">
                {MOCK_TRADERS.map((trader, i) => (
                    <motion.div
                        key={trader.rank}
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ delay: i * 0.1 }}
                        className={`glass-glamor rounded-2xl p-4 border ${trader.rank <= 3 ? 'border-white/20' : 'border-white/10'
                            } hover:bg-white/5 transition cursor-pointer`}
                    >
                        <div className="flex items-center gap-4 mb-3">
                            {/* Rank Badge */}
                            <div className={`relative h-12 w-12 rounded-xl ${getMedalGradient(trader.rank)} ${getMedalGlow(trader.rank)} flex items-center justify-center shrink-0`}>
                                <span className="text-xl font-black text-white drop-shadow-lg">#{trader.rank}</span>
                            </div>

                            {/* Trader Info */}
                            <div className="flex-1 min-w-0">
                                <div className="text-sm font-bold text-white truncate">{trader.name}</div>
                                <div className="flex items-center gap-2 text-xs text-gray-500">
                                    <span>{trader.followers.toLocaleString()} followers</span>
                                </div>
                            </div>

                            {/* Avatar */}
                            <div className="h-10 w-10 rounded-full bg-gradient-to-br from-blue-500 to-purple-500 flex items-center justify-center text-white font-bold text-sm shrink-0">
                                {trader.avatar}
                            </div>
                        </div>

                        {/* Stats Grid */}
                        <div className="grid grid-cols-3 gap-2">
                            <div className="glass-glamor rounded-lg p-2">
                                <div className="flex items-center gap-1 mb-1">
                                    <Target size={10} className="text-green-400" />
                                    <span className="text-[9px] text-gray-600 uppercase tracking-wider">Win Rate</span>
                                </div>
                                <div className="text-sm font-bold text-green-400">{trader.winRate}%</div>
                            </div>
                            <div className="glass-glamor rounded-lg p-2">
                                <div className="flex items-center gap-1 mb-1">
                                    <TrendingUp size={10} className="text-blue-400" />
                                    <span className="text-[9px] text-gray-600 uppercase tracking-wider">ROI</span>
                                </div>
                                <div className="text-sm font-bold text-blue-400">{trader.roi}%</div>
                            </div>
                            <div className="glass-glamor rounded-lg p-2">
                                <div className="flex items-center gap-1 mb-1">
                                    <Zap size={10} className="text-purple-400" />
                                    <span className="text-[9px] text-gray-600 uppercase tracking-wider">Pips</span>
                                </div>
                                <div className="text-sm font-bold text-purple-400">{trader.totalPips.toLocaleString()}</div>
                            </div>
                        </div>

                        {/* Copy Button */}
                        {trader.rank <= 3 && (
                            <motion.button
                                whileHover={{ scale: 1.02 }}
                                whileTap={{ scale: 0.98 }}
                                className="w-full mt-3 py-2 rounded-lg bg-gradient-to-r from-blue-600 to-purple-600 text-white text-xs font-bold hover:from-blue-500 hover:to-purple-500 transition"
                            >
                                Copy This Trader
                            </motion.button>
                        )}
                    </motion.div>
                ))}
            </div>

            {/* Your Rank */}
            <div className="mt-6 pt-6 border-t border-white/10">
                <div className="glass-glamor rounded-xl p-4 border border-blue-500/30 glow-accent">
                    <div className="flex items-center justify-between">
                        <div>
                            <div className="text-xs text-gray-500 mb-1">Your Rank</div>
                            <div className="text-2xl font-black text-white">#47</div>
                        </div>
                        <div className="text-right">
                            <div className="text-xs text-gray-500 mb-1">Climb to Top 10</div>
                            <div className="text-sm font-bold text-blue-400">+3,200 pips needed</div>
                        </div>
                    </div>
                </div>
            </div>
        </motion.div>
    );
};
